import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Mail, Phone, CheckCircle } from 'lucide-react';
import SEO from '../components/SEO';

const ContactPage = () => {
  // State for form fields
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');
  // State for submission status: 'idle', 'sending', 'success', 'error'
  const [status, setStatus] = useState('idle');

  const handleSubmit = async (e) => {
    e.preventDefault();
    setStatus('sending');

    try {
      const response = await fetch('https://doc2ai.ch/webhook/53394872-b923-41a0-a7ac-6ba7d1e385a7', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ name, email, message }),
      });

      if (response.ok) {
        setStatus('success');
        // Reset form fields after successful submission
        setName('');
        setEmail('');
        setMessage('');
      } else {
        // Handle non-ok responses (e.g., 4xx, 5xx)
        console.error('Webhook response not OK:', response);
        setStatus('error');
      }
    } catch (error) {
      console.error('Error sending message:', error);
      setStatus('error');
    }
  };

  const structuredData = {
    "@context": "https://schema.org",
    "@type": "ContactPage",
    "name": "Contact - Chatbot IA Suisse",
    "description": "Contactez-nous pour votre projet d'assistant IA. Devis gratuit, conseil personnalisé. Téléphone, email, formulaire de contact.",
    "mainEntity": {
      "@type": "Organization",
      "name": "Chatbot IA Suisse",
      "contactPoint": [
        {
          "@type": "ContactPoint",
          "telephone": "+41791278371",
          "contactType": "customer service",
          "email": "zenithtech@fastmail.com",
          "availableLanguage": ["French", "German", "English"]
        }
      ]
    }
  };

  return (
    <>
      <SEO 
        title="Contact - Chatbot IA Suisse | Devis Gratuit"
        description="Contactez-nous pour votre projet d'assistant IA. Devis gratuit, conseil personnalisé. Téléphone +41 79 127 83 71, email zenithtech@fastmail.com."
        keywords="contact chatbot IA, devis gratuit, conseil assistant virtuel, téléphone Suisse, projet automatisation"
        canonical="https://zenithtechdesign.github.io/chatbot-ia-suisse/contact"
        structuredData={structuredData}
      />

      <div className="py-24">
        <div className="text-center max-w-3xl mx-auto">
          <h1 className="text-4xl sm:text-5xl font-extrabold text-white">Contact</h1>
          <p className="mt-4 text-lg text-gray-400">Une question ? Un projet ? N'hésitez pas à nous contacter.</p>
        </div>

        <div className="mt-20 max-w-4xl mx-auto grid lg:grid-cols-2 gap-12">
          <div className="bg-white/5 border border-white/10 rounded-2xl p-8">
            {status === 'success' ? (
              <div className="text-center flex flex-col items-center justify-center h-full">
                <motion.div initial={{ scale: 0 }} animate={{ scale: 1 }} transition={{ type: 'spring', stiffness: 260, damping: 20 }}>
                  <CheckCircle className="text-green-400 w-16 h-16 mb-4" />
                </motion.div>
                <h2 className="text-2xl font-bold text-white">Message envoyé !</h2>
                <p className="text-gray-400 mt-2">Nous avons bien reçu votre message et nous vous répondrons dans les plus brefs délais.</p>
                 <button onClick={() => setStatus('idle')} className="mt-6 w-full bg-blue-500 hover:bg-blue-600 text-white font-bold py-3 rounded-lg transition-all duration-300">
                  Envoyer un autre message
                </button>
              </div>
            ) : (
              <AnimatePresence>
                <motion.div initial={{ opacity: 1 }} exit={{ opacity: 0 }}>
                  <h2 className="text-2xl font-bold text-white">Envoyer un message</h2>
                  <form onSubmit={handleSubmit} className="mt-6 space-y-6">
                    <div>
                      <label htmlFor="name" className="block text-sm font-medium text-gray-300">Nom *</label>
                      <input 
                        type="text" 
                        id="name" 
                        value={name}
                        onChange={(e) => setName(e.target.value)}
                        required
                        className="mt-2 w-full bg-white/10 border border-white/20 rounded-lg px-4 py-3 text-white focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent" 
                        placeholder="Votre nom complet"
                      />
                    </div>
                    <div>
                      <label htmlFor="email" className="block text-sm font-medium text-gray-300">Email *</label>
                      <input 
                        type="email" 
                        id="email" 
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        required
                        className="mt-2 w-full bg-white/10 border border-white/20 rounded-lg px-4 py-3 text-white focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent" 
                        placeholder="votre@email.com"
                      />
                    </div>
                    <div>
                      <label htmlFor="message" className="block text-sm font-medium text-gray-300">Message *</label>
                      <textarea 
                        id="message" 
                        rows="4" 
                        value={message}
                        onChange={(e) => setMessage(e.target.value)}
                        required
                        className="mt-2 w-full bg-white/10 border border-white/20 rounded-lg px-4 py-3 text-white focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent resize-none"
                        placeholder="Décrivez votre projet ou posez votre question..."
                      ></textarea>
                    </div>
                    <button 
                      type="submit" 
                      disabled={status === 'sending'}
                      className="w-full bg-blue-500 hover:bg-blue-600 text-white font-bold py-3 rounded-lg transition-all duration-300 disabled:bg-blue-800 disabled:cursor-not-allowed flex items-center justify-center"
                    >
                      {status === 'sending' ? (
                        <>
                          <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                            <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                            <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                          </svg>
                          Envoi en cours...
                        </>
                      ) : 'Envoyer le message'}
                    </button>
                    {status === 'error' && (
                      <p className="text-red-400 text-center">Une erreur s'est produite. Veuillez réessayer ou nous contacter directement.</p>
                    )}
                  </form>
                </motion.div>
              </AnimatePresence>
            )}
          </div>

          <div className="space-y-8">
            <div className="bg-white/5 border border-white/10 rounded-2xl p-8">
               <h3 className="text-xl font-semibold text-white">Informations de contact</h3>
               <div className="mt-4 space-y-4">
                  <div className="flex items-center gap-4">
                      <Mail className="text-blue-400 flex-shrink-0" size={24} />
                      <div>
                        <a href="mailto:zenithtech@fastmail.com" className="text-gray-300 hover:text-white transition-colors">zenithtech@fastmail.com</a>
                        <p className="text-sm text-gray-500">Réponse sous 24h</p>
                      </div>
                  </div>
                  <div className="flex items-center gap-4">
                      <Phone className="text-blue-400 flex-shrink-0" size={24} />
                      <div>
                        <a href="tel:+41791278371" className="text-gray-300 hover:text-white transition-colors">+41 79 127 83 71</a>
                        <p className="text-sm text-gray-500">Lun-Ven 9h-18h</p>
                      </div>
                  </div>
               </div>
            </div>

            <div className="bg-white/5 border border-white/10 rounded-2xl p-8">
               <h3 className="text-xl font-semibold text-white">Projet d'automatisation ?</h3>
               <p className="mt-2 text-gray-400">Pour les projets sur-mesure, décrivez vos besoins dans le formulaire pour un rendez-vous productif.</p>
               
               <div className="mt-4 space-y-2 text-sm text-gray-400">
                 <p>• Type d'entreprise et secteur d'activité</p>
                 <p>• Processus à automatiser</p>
                 <p>• Outils existants à connecter</p>
                 <p>• Volume de clients/visiteurs</p>
               </div>
            </div>

            <div className="bg-gradient-to-r from-blue-500/10 to-purple-500/10 border border-blue-500/20 rounded-2xl p-8">
               <h3 className="text-xl font-semibold text-white">🚀 Démarrage rapide</h3>
               <p className="mt-2 text-gray-400 mb-4">Besoin d'un assistant IA rapidement ? Notre offre standard peut être mise en place en quelques jours.</p>
               <a 
                 href="https://zenithtechdesign.github.io/chatbot-ia-commande"
                 className="inline-block bg-blue-500 hover:bg-blue-600 text-white font-semibold px-6 py-3 rounded-lg transition-all duration-300"
                 target="_blank"
                 rel="noopener noreferrer"
               >
                 Commander directement
               </a>
            </div>
          </div>
        </div>

        {/* FAQ Contact */}
        <div className="mt-20 max-w-3xl mx-auto">
          <h2 className="text-2xl font-bold text-center text-white mb-8">Questions avant de nous contacter ?</h2>
          <div className="grid md:grid-cols-2 gap-6 text-sm">
            <div className="bg-white/5 border border-white/10 rounded-lg p-4">
              <h3 className="font-semibold text-white mb-2">Combien de temps pour une réponse ?</h3>
              <p className="text-gray-400">Nous répondons à tous les messages sous 24h en semaine, souvent plus rapidement.</p>
            </div>
            <div className="bg-white/5 border border-white/10 rounded-lg p-4">
              <h3 className="font-semibold text-white mb-2">Le devis est-il gratuit ?</h3>
              <p className="text-gray-400">Oui, tous nos devis et conseils sont entièrement gratuits et sans engagement.</p>
            </div>
            <div className="bg-white/5 border border-white/10 rounded-lg p-4">
              <h3 className="font-semibold text-white mb-2">Travaillez-vous partout en Suisse ?</h3>
              <p className="text-gray-400">Oui, nous travaillons avec des entreprises dans toute la Suisse, à distance ou sur site.</p>
            </div>
            <div className="bg-white/5 border border-white/10 rounded-lg p-4">
              <h3 className="font-semibold text-white mb-2">Puis-je voir une démo ?</h3>
              <p className="text-gray-400">Bien sûr ! Testez notre chatbot en bas à droite ou demandez une démo personnalisée.</p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default ContactPage;
