import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, useLocation } from 'react-router-dom';
import { HelmetProvider } from 'react-helmet-async';
import { motion, AnimatePresence } from 'framer-motion';

// Components
import Header from './components/Header';
import Footer from './components/Footer';
import { ParticleBackground, ShaderBackground } from './components/Backgrounds';

// Pages
import HomePage from './pages/HomePage';
import ServicesPage from './pages/ServicesPage';
import TarifsPage from './pages/TarifsPage';
import ContactPage from './pages/ContactPage';

// Background selector component
const BackgroundSelector = () => {
  const location = useLocation();
  const currentPath = location.pathname;

  return (
    <AnimatePresence>
      {currentPath === '/' && (
        <motion.div key="home-bg" exit={{ opacity: 0 }} transition={{ duration: 1 }}>
          <ParticleBackground />
        </motion.div>
      )}
      {currentPath === '/services' && (
        <motion.div key="services-bg" initial={{ opacity: 0 }} animate={{ opacity: 1 }} exit={{ opacity: 0 }} transition={{ duration: 1 }}>
          <ShaderBackground type="services" />
        </motion.div>
      )}
      {currentPath === '/tarifs' && (
        <motion.div key="tarifs-bg" initial={{ opacity: 0 }} animate={{ opacity: 1 }} exit={{ opacity: 0 }} transition={{ duration: 1 }}>
          <ShaderBackground type="tarifs" />
        </motion.div>
      )}
      {currentPath === '/contact' && (
        <motion.div key="contact-bg" initial={{ opacity: 0 }} animate={{ opacity: 1 }} exit={{ opacity: 0 }} transition={{ duration: 1 }}>
          <ShaderBackground type="contact" />
        </motion.div>
      )}
    </AnimatePresence>
  );
};

// Page wrapper component for animations
const PageWrapper = ({ children }) => {
  const location = useLocation();
  
  return (
    <AnimatePresence mode="wait">
      <motion.div 
        key={location.pathname}
        initial={{ opacity: 0, y: 20 }} 
        animate={{ opacity: 1, y: 0 }} 
        exit={{ opacity: 0, y: -20 }} 
        transition={{ duration: 0.5 }}
      >
        {children}
      </motion.div>
    </AnimatePresence>
  );
};

// Main App Layout component
const AppLayout = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  // Close menu when route changes
  const location = useLocation();
  useEffect(() => {
    setIsMenuOpen(false);
    window.scrollTo(0, 0);
  }, [location.pathname]);

  return (
    <div className="text-gray-200 font-['Inter',_sans-serif] antialiased">
      <BackgroundSelector />
      <div className="relative z-10">
        <Header isMenuOpen={isMenuOpen} setIsMenuOpen={setIsMenuOpen} />
        <main className="px-4 sm:px-6 lg:px-8">
          <PageWrapper>
            <Routes>
              <Route path="/" element={<HomePage />} />
              <Route path="/services" element={<ServicesPage />} />
              <Route path="/tarifs" element={<TarifsPage />} />
              <Route path="/contact" element={<ContactPage />} />
            </Routes>
          </PageWrapper>
        </main>
        <Footer />
      </div>
    </div>
  );
};

// Main App component
const App = () => {
  useEffect(() => {
    // Load external scripts and styles
    const tailwindScript = document.createElement('script');
    tailwindScript.src = "https://cdn.tailwindcss.com";
    document.head.appendChild(tailwindScript);

    const gtmScript = document.createElement("script");
    gtmScript.innerHTML = `(function(w,d,s,l,i){w[l]=w[l]||[];w[l].push({'gtm.start':new Date().getTime(),event:'gtm.js'});var f=d.getElementsByTagName(s)[0],
j=d.createElement(s),dl=l!='dataLayer'?'&l='+l:'';j.async=true;j.src='https://www.googletagmanager.com/gtm.js?id='+i+dl;f.parentNode.insertBefore(j,f);
})(window,document,'script','dataLayer','GTM-TL3NZSML');`;
    document.head.appendChild(gtmScript);

    const fontLink1 = document.createElement('link');
    fontLink1.href = "https://fonts.googleapis.com";
    fontLink1.rel = "preconnect";
    document.head.appendChild(fontLink1);

    const fontLink2 = document.createElement('link');
    fontLink2.href = "https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700;800&display=swap";
    fontLink2.rel = "stylesheet";
    document.head.appendChild(fontLink2);

    // Set body background
    document.body.style.backgroundColor = '#0A0A0A';

    return () => { 
      if (gtmScript.parentNode) document.head.removeChild(gtmScript);
      if (tailwindScript.parentNode) document.head.removeChild(tailwindScript);
      if (fontLink1.parentNode) document.head.removeChild(fontLink1);
      if (fontLink2.parentNode) document.head.removeChild(fontLink2);
    }
  }, []);

  return (
    <HelmetProvider>
      <Router basename="/chatbot-ia-suisse">
        <>
          <noscript>
            <iframe src="https://www.googletagmanager.com/ns.html?id=GTM-TL3NZSML" height="0" width="0" style={{ display: 'none', visibility: 'hidden' }} title="Google Tag Manager"></iframe>
          </noscript>
          <AppLayout />
          <style>{`
            body { 
              background-color: #0A0A0A; 
            }
            /* Add a safe area at the bottom on mobile to prevent the browser UI from hiding fixed elements */
            @media (max-width: 768px) {
              body {
                padding-bottom: 80px;
              }
            }
          `}</style>
        </>
      </Router>
    </HelmetProvider>
  );
};

export default App;
